
// Layouts
import GuestLayout from '@/layouts/GuestLayout';
// Pages
import Home from '@/pages/Home';
import Contact from '@/pages/Contact';

export const routes = [
    {
      path: '/',
      component:GuestLayout,
      children:[
		{
			path:'',
			name: 'Home',
			component: Home
		},
		{
			path: '/contact',
			name: 'Contact',
			component: Contact
		}
      ]
    },
    
];




