<template>
	<div>
		<navbar :items="navbar.items"/>
		<v-content>
	      	<transition name="fade" mode="out-in" appear>
				<router-view />
			</transition>
	    </v-content>
	</div>
</template>
<script>
	import navbar from '../components/Navbar.vue';
	export default{
		components:{navbar, },
		mounted()
		{
			document.title= this.website.title;
		},
		data:()=>({
			navbar:{
				items:[
					{
						text:'Inicio',
						icon:'home',
						go:{ name:'Home'},

					},
					{
						text:'Acerca de',
						icon:'people',
						go:{ name:'About'}
					},
					{
						text:'Contacto',
						icon:'email',
						go:{ name:'Contact'}
					}
				]
			}
		}),
		watch:{
			'$route'(to,from){
				document.title = this.website.title;
			}
		},
		computed:{
			website()
			{
				return this.$store.getters.getWebsiteInfo;
			}
		}
	}
</script>
<style lang="scss">
	
</style>


