<template>
	<i v-bind:class="[type,icon,size]" class="right" v-if="right"></i>
	<i v-bind:class="[type,icon,size]" class="left" v-else></i>
</template>
<script>
	export default{
		props:["type","icon","size","right"]
	}
</script>
<style lang="scss" scoped>
	i
	{
		
	}
	.left
	{
		margin-right: 5px !important;
	}
	.right
	{
		margin-left: 5px !important;
	}
	.big
	{
		font-size: 40px;
	}
	.meidum
	{
		font-size: 30px;
	}
	.small
	{
		font-size: 20px;
	}
</style>


