<template>
	<div>
		<v-navigation-drawer
	      v-model="drawer"
	      fixed
	      class="hidden-md-and-up"
	    >
	      <v-list dense>
	        <v-list-tile v-for="(item, index) in items" @click="goTo(item.go)" :key="index">
	          <v-list-tile-action>
	            <v-icon>{{ item.icon }}</v-icon>
	          </v-list-tile-action>
	          <v-list-tile-content>
	            <v-list-tile-title>{{  item.text }}</v-list-tile-title>
	          </v-list-tile-content>
	        </v-list-tile>
	      </v-list>
	    </v-navigation-drawer>
	    <v-toolbar color="blue darken-2" dark fixed app>
	      <v-toolbar-side-icon class="hidden-md-and-up" @click.stop="drawer = !drawer"></v-toolbar-side-icon>
	      <v-toolbar-title>{{ website.title }}</v-toolbar-title>
	      <v-spacer></v-spacer>
	      <v-toolbar-items class="hidden-sm-and-down">
	      	<v-btn flat v-for="(item, index) in items" :key="index" @click="goTo(item.go)">
	      		{{ item.text }}
	      	</v-btn>
	      </v-toolbar-items>
	    </v-toolbar>
	    
	</div>
</template>
<script>
export default{
	props:['items'],
	data(){
		return{
			drawer: null
		}
	},
	methods:{
		goTo(param)
		{
			this.$router.push(param);
		}
	},
	computed:{
		website()
		{
			return this.$store.getters.getWebsiteInfo;
		}
	}
}
</script>
<style lang="scss">
	
</style>


