<template>
	<div class="HomePage">
		<div class="desktop hidden-sm-and-down">
			<v-container fill-height>
		      	<v-layout align-center>
		        	<v-flex text-xs-center>
		        		<v-progress-circular v-show="loader" fixed :size="50" :width="5" color="primary" indeterminate></v-progress-circular>
		        		<br>
		        		<v-tooltip left v-show="!loader">
		        			<v-btn @click="OpenDialog(Dialog.MyProfile)" slot="activator" color="deep-orange" dark fab icon large class="btn-effect" v-bind:class="[buttons.MyProfile ? 'btn-visible' : '']" >
								<v-icon large>person</v-icon>
		        			</v-btn>
		        			<span>Sobre mí</span>
		        		</v-tooltip>
						<v-tooltip top v-show="!loader">
							<v-btn @click="OpenDialog(Dialog.MyProjects)" slot="activator" class="btn-effect" color="red" dark icon v-bind:class="[buttons.MyProjects ? 'btn-visible' : '']" fab large>
								<v-icon large>view_carousel</v-icon>
							</v-btn>
							<span>Mis proyectos</span>
						</v-tooltip>
						<v-tooltip right v-show="!loader">
							<v-btn  @click="OpenDialog(Dialog.MyTechsStack)" icon dark color="green lighten-1" slot="activator" fab large class="btn-effect" v-bind:class="[buttons.MyTechsStack ? 'btn-visible' : '']">
								<v-icon large>build</v-icon>
							</v-btn>
							<span>Mi stack de tecnologias</span>
						</v-tooltip>
					</v-flex>
				</v-layout>
			</v-container>
		</div>
		<div class="mobile hidden-md-and-up">
			<v-container fill-height>
		      	<v-layout align-center>
		        	<v-flex text-xs-center>
						<v-progress-circular v-show="loader" fixed :size="50" :width="5" color="primary" indeterminate></v-progress-circular>
		        		<br>
		        		<v-tooltip left v-show="!loader">
		        			<v-btn @click="OpenDialog(Dialog.MyProfile)" slot="activator" color="deep-orange" dark fab icon large class="btn-effect" v-bind:class="[buttons.MyProfile ? 'btn-visible' : '']" >
								<v-icon large>person</v-icon>
		        			</v-btn>
		        			<span>Sobre mí</span>
		        		</v-tooltip>
						<v-tooltip top v-show="!loader">
							<v-btn @click="OpenDialog(Dialog.MyProjects)" slot="activator" class="btn-effect" color="red" dark icon v-bind:class="[buttons.MyProjects ? 'btn-visible' : '']" fab large>
								<v-icon large>view_carousel</v-icon>
							</v-btn>
							<span>Mis proyectos</span>
						</v-tooltip>
						<v-tooltip right v-show="!loader">
							<v-btn  @click="OpenDialog(Dialog.MyTechsStack)" icon dark color="green lighten-1" slot="activator" fab large class="btn-effect" v-bind:class="[buttons.MyTechsStack ? 'btn-visible' : '']">
								<v-icon large>build</v-icon>
							</v-btn>
							<span>Mi stack de tecnologias</span>
						</v-tooltip>
					</v-flex>
				</v-layout>
			</v-container>
			<v-dialog scrollable v-model="Dialog.MyProfile.active">
				<v-card>
					<v-card-title>
						<span>Sobre mí</span>
					</v-card-title>
					<v-card-text style="height: 100vh;">
						<v-container>
							<p>
								Nombre completo: Ariel Lautaro Umpierrez Guillermo
							</p>
							<p>
								Redes sociales: 
							</p>
							<center>
								<v-btn @click="goTo('https://www.facebook.com/lautaro.umpierrez.1')" dark color="blue">
								 	<font-awesome-icon :type="'fab'" icon="fa-facebook" size="small" :right="false"/> 
									Facebook
								</v-btn>
								<v-btn dark color="red lighten-1" @click="goTo('https://www.instagram.com/lautaumpierrez/')">
								    <font-awesome-icon :type="'fab'" icon="fa-instagram" size="small" :right="false"/> 

									Instagram
								</v-btn>
								<v-btn dark color="deep-orange darken-5" @click="goTo('https://github.com/lautaumpierrez')">
									    <font-awesome-icon :type="'fab'" icon="fa-github" size="small" :right="false"/> Github
								</v-btn>
							</center>
							<br>
							<p>
								Edad: <strong>14 años</strong>
							</p>
							<p>
								Nacionalidad: Uruguayo
							</p>
							<p>
								<h2 class="headline">Hobby: </h2>
								Me encanta programar una vez termino todas mis actividades cotidianas.
								Hace poco comenzé  a ilustrar con vectores, descubrí que amo crear mis pequeñas obras de arte y además, complementan muy bien mis proyectos personales, ya que me permiten crear yo mismo los logos de mis aplicaciones.
							</p>
							<p>
								<h2 class="headline">¿Como Soy?</h2>
								Amo aprender todo el tiempo, paso muchas horas en el computador aprendiendo mejores practicas al programar y mejorando las que ya utilizo, soy un apasionado de js y laravel pero estoy abierto a nuevas tecnologías.
								Soy una persona responsable y que sabe lo que quiere, no tengo problemas al desarrollar de forma individual, pero disfruto mucho programar en equipo ( o desarrollar un producto no necesariamente en codigo. )
							</p>
						</v-container>
				</v-card-text>
					<v-divider/>
					<v-card-actions>
						<v-btn  color="primary" flat @click="Dialog.MyProfile.active=false">Cerrar</v-btn>
					</v-card-actions>
				</v-card>
			</v-dialog>
			<v-dialog scrollable v-model="Dialog.MyProjects.active" max-width="600px">
				<v-card v-if="Dialog.MyProjects.active">
					<v-card-title>
						<span>Mis Proyectos</span>
					</v-card-title>
					<v-card-text>
						<v-card v-if="Dialog.MyProjects.active">
					        <v-container grid-list-sm fluid>
					          <v-layout row wrap>
					            <v-flex
					              v-for="(item,index) in Dialog.MyProjects.items"
					              :key="index"
					              xs12
					              md6
					              md6
					              d-flex
					              @click="goTo(item.href)"
					            >
					            <v-tooltip top>
					              <v-card :style="{'margin-left':'10px','margin-top':'30px','cursor':'pointer'}" slot="activator" tile class="d-flex">
					              	<v-img
					                  class="hidden-sm-and-down grey lighten-2"
					                  :src="item.img"
					                  aspect-ratio="1.25">
						                <v-layout
						                    slot="placeholder"
						                    fill-height
						                    align-center
						                    justify-center
						                    ma-0
						                  >
						                    <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
						                  </v-layout>
					                </v-img>
					                <v-img
					                  :src="item.img"
					                  :aspect-ratio="item['aspect-ratio']"
					                  class="hidden-md-and-up grey lighten-2"
					                >
					                
					                  <v-layout
					                    slot="placeholder"
					                    fill-height
					                    align-center
					                    justify-center
					                    ma-0
					                  >
					                    <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
					                  </v-layout>
					                </v-img>
					              </v-card>
					              <span>{{ item.text }}</span>
					          	 </v-tooltip>
					            </v-flex>
					          </v-layout>
					        </v-container>
					      </v-card>
					</v-card-text>
					<v-card-actions>
						<v-btn color="primary" flat @click="Dialog.MyProjects.active=false">Close</v-btn>
					</v-card-actions>
				</v-card>
			</v-dialog>
			<v-dialog scrollable v-model="Dialog.MyTechsStack.active" max-width="500px">
				<v-card>
					<v-card-title>
						<span>Mi stack de tecnologías</span>
						<v-spacer></v-spacer>
					</v-card-title>
					<v-card-text :style="{'padding-bottom':'30px'}">
						<v-subheader>LENGUAJES</v-subheader>
						<v-tooltip left>
							<v-btn large icon flat fab slot="activator" class="yellow--text">
								<font-awesome-icon :type="'fab'" icon="fa-js" size="big"/>
							</v-btn>
							<span>Javascript</span>
						</v-tooltip>
						<v-tooltip top>
							<v-btn large icon flat fab slot="activator" class="red--text">
								<font-awesome-icon :type="'fab'" icon="fa-sass" size="big" :right="false"/>
							</v-btn>
							<span>Sass</span>
						</v-tooltip>
						<v-tooltip top>
							<v-btn large icon flat fab slot="activator" class="blue--text">
								<font-awesome-icon :type="'fab'" icon="fa-css3-alt" size="big" :right="false"/>
							</v-btn>
							<span>CSS3</span>
						</v-tooltip>
						<v-tooltip top>
							<v-btn large icon flat fab slot="activator" class="blue--text">
								<font-awesome-icon :type="'fab'" icon="fa-php" size="big"/>
							</v-btn>
							<span>Php5.6+</span>
						</v-tooltip>
						<v-subheader>FRAMEWORKS</v-subheader>
						<v-tooltip bottom>
							<v-btn large icon flat fab slot="activator" class="green--text">
								<font-awesome-icon :type="'fab'" icon="fa-vuejs" size="big" :right="false"/>
							</v-btn>
							<span>VueJS</span>
						</v-tooltip>
						<v-tooltip bottom>
							<v-btn large icon flat fab slot="activator" class="red--text">
								<font-awesome-icon :type="'fab'" icon="fa-laravel" size="big"/>
							</v-btn>
							<span>Laravel</span>
						</v-tooltip>
						<v-tooltip bottom>
							<v-btn large icon flat fab slot="activator" class="green--text">
								<font-awesome-icon :type="'fab'" icon="fa-android" size="big" :right="false"/>
							</v-btn>
							<span>Desarrollo para android con cordova</span>
						</v-tooltip>
					</v-card-text>
					<v-card-actions>
						<v-btn color="primary" flat @click="Dialog.MyTechsStack.active=false">Close</v-btn>
					</v-card-actions>
				</v-card>
			</v-dialog>
		</div>
	</div>
</template>
<script>
	import fontawesomeicon from '../components/font-awesome-icon.vue';
	import DistroBiarritzImgFile from '../assets/distrobiarritz.png';
	import XionCorporationImgFile from '../assets/xioncorporation.png';
	export default{
		components:{'font-awesome-icon':fontawesomeicon},
		mounted()
		{
			this.startShowing();
		},
		data:()=>({
			buttons:{
				MyProfile:false,
				MyProjects:false,
				MyTechsStack:false
			},
			Dialog:{
				MyProfile:{
					active:false,
				},
				MyProjects:{
					active:false,
					items:[
						{
							img: DistroBiarritzImgFile,
							href:'https://distribuidorabiarritz.github.io',
							text:'Distribuidora Biarritz Page (WebApp y API Restful ) Click para abrir.',
							'aspect-ratio':'1.25'
						},
						{
							img: XionCorporationImgFile,
							href:'https://xioncorporation.net',
							text:'XionCorporation Software, Click para abrir.',
							'aspect-ratio':'2.1'
						}
					],

				},
				MyTechsStack:{
					active: false,
				}
			},
			showInterval: null,
			loader:true,
		}),
		methods:{
			startShowing()
			{
				var thisObj = this;
				var vueltas = 0;
				setTimeout(function(){
					thisObj.loader = false;
				},450);
				this.showInterval = window.setInterval(()=>{
					switch(vueltas)
					{
						case 0:
							thisObj.buttons.MyProfile =true;
							break;
						case 1:
							thisObj.buttons.MyProjects =true;
							break;
						case 2:
							thisObj.buttons.MyTechsStack =true;
							break;
					}
					if(vueltas >= 2)
					{
						thisObj.stopShowing();
					}
					console.log(vueltas)
					vueltas = vueltas+1;
				},500);
			},
			OpenDialog(object)
			{
				object.active = true;
			},
			stopShowing()
			{
				clearInterval(this.showInterval);
			},
			goTo(url)
			{
				window.open(url,'blank')
			}
		}
	}
</script>
<style lang="scss">
	.HomePage
	{
		.desktop{
			height: calc(100vh - 69px) !important;
			.v-btn
			{
				width: 15vw;
				height: 15vw;
				border-radius: 50vw !important;
				i{
					font-size: 60px !important;
				}
			}
		}
		.mobile{
			height: calc(100vh - 48px) !important;
		}
		.btn-visible{
			transition: all 0.3s !important;
			opacity: 1 !important;
		}

		.btn-effect{
			transition: all 0.3s !important;
			opacity: 0;
		}
	}
</style>



