import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
export const store =  new Vuex.Store({
	state:{
		auth:true,
		website:{
			title:'Lautaro Umpierrez'
		}
	},
	mutations:{
		cambiar:(state)=>state.auth = !state.auth,
	},
	getters:{
		getAuth(state)
		{
			return state.auth;
		},
		getWebsiteInfo(state)
		{
			return state.website;
		}
	}
});


